import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';


let BOX = styled.div`
    padding : 20px;
`;

let H4title = styled.h4`
    font-size : 25px;
    color : ${props => props.fcolor}
`;


function Detail(props){
    
    let [AlertTimer,setAlertTimer] = useState(true);
    let { id }  = useParams();
    let history = useHistory();
    let [inputData,setinputData] = useState('');
    
    let findshoes = props.shoes.find(function(shoes){
      return shoes.id == id
    });

    function Jumon(i){
      let jeagocopy = [...props.jeago];
      jeagocopy[i]--;
      props.setJeago(jeagocopy);
    }


    useEffect(()=>{
      let Timer = setTimeout(()=>{
        setAlertTimer(false);
        
        return ()=>{ clearTimeout(Timer) }
      },2000);
    }, [AlertTimer]);


    return(
      <div className="container">
          <BOX>
            <H4title className="red" fcolor={"blue"}>Detail</H4title>
            <H4title fcolor={"red"}>Detail</H4title>
          </BOX>
          {inputData}
          <input onChange={ (e)=>{ setinputData(e.target.value) } }/>
          { AlertTimer ? <AlertBox></AlertBox> : null }
              <div className="row">
                <div className="col-md-6">
                  <img src={"https://codingapple1.github.io/shop/shoes"+(findshoes.id+1)+".jpg"} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                  <h4 className="pt-5">{findshoes.title}</h4>
                  <p>{findshoes.content}</p>
                  <p>{findshoes.price}</p>
                  <Info jeago = {props.jeago} i={findshoes.id} ></Info>
                  <button className="btn btn-danger" onClick = { () => {Jumon(findshoes.id)}}>주문하기</button> 
                  <button className="btn btn-danger" onClick={()=>{
                      history.goBack();
                  }}>뒤로가기</button> 
                </div>
              </div>
        </div> 
    )
  }

  function AlertBox(){
    return (
      <div className="my_alert_red">
        <p>재고가 얼마 남지 않았습니다.</p>
      </div>
    )
  }

  function Info(props){
    return(
      <p>재고: {props.jeago[props.i]}</p>
    )

  }

  export default Detail;